











































export default {
	data() {
		return {
			Islogin: false,
			avatarUrl: "../../../static/uni-center/logo.png",
			userinfo: {}
		}
	},
	methods: {
		goLogin() {
			if (!this.Islogin) {
				uni.navigateTo({
					url: '../../basiclogin/login'
				});
			}
		}
	},
	onShow() {
		var me = this;
		// 使用挂载方法获取用户数据
		me.getGlobalUser(function(data){
			if(data!=null){
				me.Islogin = true;
				me.userinfo = data;
			}else{
				me.Islogin = false;
				me.userinfo = {};					
			}
		});
	}
}
